@import '../../Style/Utils/variables';

.div_card-logement {
    display: flex;
    width: 335px;
    height: 255px;
    min-width: 335px;
    position: relative;
    border-radius: 10px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(10, 10, 10, 0) 42%, rgba(4, 4, 4, 0.205) 99.99%, rgba(0, 0, 0, 0.5) 100%);

}

.div_card-logement img {
    display: flex;
    width: 100%;
    border-radius: 10px;
    object-fit: cover;
}

.div_card-title {
    display: flex;
    color: $white-text;
    font-size: 15px;
    align-items: flex-end;
    position: absolute;
    line-height: 32;
    margin-left: 10px;
    font-weight: 700;
}

